<script>
import { mapState } from 'vuex'
import { Toast, MessageBox } from 'mint-ui'
export default {
  data () {
    return {
      emailState: '', // 邮箱检验的状态
      passwordState: '', // 检验密码格式
      nickNameState: '', // 检验昵称格式
      email: '', // 邮箱地址
      nickName: '',
      username: '', // 用户名
      password: '', // 密码
      captcha: '', // 验证码
      isLogin: true, // 是否登录
      isForget: false, // 是否忘记密码
      agreeValue: false, // 注册时是否同意协议
      popupVisible: false, // 弹出层
      base64Data: '', // 获取的base64的data
      codeId: '', // 获取的验证码id
      emailCode: '' // 邮箱验证码
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      userName: states => states.userName
    })
  },
  created () {
  },
  mounted () {
    if (this.userName) {
      this.email = this.uesrName
    }
  },
  watch: {
    email: function () {
    },
    password: function () {
    },
    agreeValue: function (newVal, oldVal) {
    },
    isLogin: function (newVal, oldVal) {
      if (!newVal) {
        this.getCodeSrc()
      }
      this.email = ''
      this.password = ''
      this.emailCode = ''
    },
    isForget: function (newVal, oldVal) {
      this.getCodeSrc()
      this.email = ''
      this.password = ''
      this.emailCode = ''
    }
  },
  methods: {
    login () {
      this.$store.dispatch('setLogin', {
        userName: '',
        passWord: ''
      })
    },
    emailBlur () {
      console.log('--blur--')
      // MessageBox('notice', 'you click this button')
    },
    getCodeSrc () {
      let _this = this
      this.$store.dispatch('getCodebase64', {
        cb: function (data) {
          _this.base64Data = data.pngBase64
          _this.codeId = data.codeId
        }
      })
    },
    loginFn () {
      if (!this.email) {
        MessageBox('', '请输入账号邮箱')
      }
      else if (!this.password) {
        MessageBox('', '请输入账号密码')
      }
      else {
        this.setLoginFn()
      }
    },
    toRegist () {
      this.isLogin = false
      this.isForget = false
    },
    registFn () {
      let _this = this
      // 字母数字特殊字符
      // let regex = new RegExp('(?=.*[0-9])(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{6,20}')
      let reg = /^[A-Za-z0-9]{6,20}$/  // 6-20 字母数字组合
      if (!reg.test(this.password)) {
        MessageBox('提示', '密码需由6-20位数字和字母组合而成！')
        return
      }
      if (this.agreeValue) { // 同意协议的情况下
        // 调取登录接口，如果拿到数据，跳转到 home
        _this.$store.dispatch('setRegist', {
          params: {
            userName: _this.email,
            passWord: _this.password,
            verifyCode: _this.emailCode,
            nickName: _this.nickName
          },
          cb: function (data) {
            if (data.errors || !data.success) { // 注册失败
              Toast({
                message: data.message,
                position: 'middle',
                duration: 2000
              })
            }
            if (data.memberId) { // 注册成功
              // 注册成功后调用登录的接口，成功登录
              _this.setLoginFn()
            }
          }
        })
      }
      else { // 不同意协议的情况下，提示用户点击同意协议
        Toast({
          message: '请先同意协议',
          position: 'middle',
          duration: 2000
        })
      }
    },
    forgetFn () {
      this.isForget = true
    },
    forgetBackLogin () {
      this.isForget = false
    },
    registBackLogin () {
      this.isLogin = true
      this.isForget = false
    },
    changeLanguage () {
      this.$store.commit('ENGLISH', !this.english)
    },
    forgetOKFn () {
      let _this = this
      // 验证密码格式
      let reg = /^[A-Za-z0-9]{6,20}$/
      if (!reg.test(this.password)) {
        MessageBox('提示', '密码需由6-20位数字和字母组合而成！')
        return
      }
      // 调接口
      this.$store.dispatch('setForgetpassword', {
        params: {
          userName: _this.email,
          newPassword: _this.password,
          verifyCode: _this.emailCode
        },
        cb: function (data) {
          console.log(data)
          if (!data.success) { // 修改密码报错
            Toast({
              message: data.message,
              position: 'middle',
              duration: 2000
            })
          }
          else { // 修改密码正确
            // 接下来直接用账号密码登录
            _this.$store.dispatch('setLogin', {
              userName: _this.email,
              passWord: _this.password,
              cb: function (data) {
                // console.log(data)
              }
            })
            _this.$router.push({meta: {auth: true}, path: '/home'})
          }
        }
      })
    },
    sendEmailCode () {
      let _this = this
      if (this.email === '') {
        Toast({
          message: '请输入邮箱',
          position: 'middle',
          duration: 2000
        })
      }
      else {
        this.$store.dispatch('getSendemail', {
          params: {
            email: _this.email
          },
          cb: function (data) {
            console.log(data)
          }
        })
      }
    },
    setLoginFn () {
      let _this = this
      // 登录接口
      this.$store.dispatch('setLogin', {
        userName: _this.email,
        passWord: _this.password,
        cb: function (data) {
          if (data.memberId) {
            _this.$router.push({meta: {auth: true}, path: '/home'})
          }
          else {
            Toast({
              message: data.message,
              position: 'middle',
              duration: 2000
            })
          }
        }
      })
    }
  },
  components: {
  }
}
</script>
<template>
  <div class="login page">
    <!-- 登录 -->
    <div class="login-con" v-if="isLogin">
      <div class="lang">
        <mt-button class="iconfont" :class="{'icon-yingwenyuyan': english, 'icon-zhongwenyuyan': !english}" @click.native="changeLanguage"></mt-button>
      </div>
      <!-- 正常登录 -->
      <div v-if="!isForget"> 
        <div class="tac login-btn">
          <div class="login-logo tac">
            <span v-if="!english">登录</span>
            <span v-else>Login</span>
          </div>
          <div class="login-con-header-title">
            <span v-if="!english">太上熊猫</span>
            <span v-else>tsxm</span>
          </div>
        </div>
        <div>
          <div v-if="!english">
            <mt-field class="login-field" label="邮箱" placeholder="请输入邮箱" :state="emailState" v-model="email" @blur="emailBlur()"></mt-field>
            <mt-field class="login-field" label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
          </div>
          <div v-else>
            <mt-field class="login-field" label="Email" placeholder="Please enter the email" :state="emailState" v-model="email" @blur="emailBlur()"></mt-field>
            <mt-field class="login-field" label="Password" placeholder="Please enter the password" type="password" v-model="password"></mt-field>          
          </div>
          <div class="tac login-btn">
            <mt-button type="default" @click.native="loginFn"><span v-if="!english">登录</span><span v-else>Login</span></mt-button>
            <mt-button type="default" @click.native="toRegist"><span v-if="!english">注册</span><span v-else>Regist</span></mt-button>
            <mt-button type="default" @click.native="forgetFn"><span v-if="!english">忘记密码</span><span v-else>Forget</span></mt-button>
          </div>
        </div>
      </div>
      <!-- 忘记密码 -->
      <div v-else>
        <div>
          <div class="tac login-btn">
            <div class="login-logo">
              <span v-if="!english">忘记密码</span>
              <span v-else>Forget</span>
            </div>
            <div class="login-con-header-title">
              <span v-if="!english">太上熊猫</span>
              <span v-else>tsxm</span>
            </div>
          </div>
          <div class="login-con-body">
            <div v-if="!english">
              <mt-field 
                class="login-field" 
                label="邮箱" 
                placeholder="请输入邮箱" 
                @mouseout.native="sendEmailCode"
                v-model="email">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="邮箱验证码" 
                placeholder="请输入邮箱验证码" 
                v-model="emailCode">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="请输入新密码" 
                type="password" 
                v-model="password">
              </mt-field>
              <!-- <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="请再次输入新密码" 
                type="password" 
                v-model="password">
              </mt-field> -->
              <!-- <mt-field 
                class="login-field" 
                label="验证码" v-model="captcha">
                <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
              </mt-field> -->
            </div>
            <div v-else>
              <mt-field 
                class="login-field" 
                label="邮箱" 
                placeholder="Please enter the email" 
                @mouseout.native="sendEmailCode"
                v-model="email">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="email code" 
                placeholder="Please enter the email code" 
                v-model="emailCode">
              </mt-field>
              <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="Please enter a new password" 
                type="password" 
                v-model="password">
              </mt-field>
              <!-- <mt-field 
                class="login-field" 
                label="密码" 
                placeholder="Please enter your new password again" 
                type="password" 
                v-model="password">
              </mt-field> -->
              <!-- <mt-field 
                class="login-field" 
                label="验证码" v-model="captcha">
                <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
              </mt-field> -->
            </div>
            <div class="tac login-btn">
              <mt-button 
                type="default" 
                @click.native="forgetOKFn">
                <span v-if="!english">确定</span>
                <span v-else>OK</span>
              </mt-button>
              <mt-button 
                type="default" 
                @click.native="forgetBackLogin">
                <span v-if="!english">返回登录</span>
                <span v-else>Back</span>
              </mt-button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 注册第一步 第二步 -->
    <div class="login-con" v-else>
      <div>
        <div class="tac login-btn">
          <div class="login-logo">
            <span v-if="!english">注册</span>
            <span v-else>Regist</span>
          </div>
          <div class="login-con-header-title">
            <span v-if="!english">太上熊猫</span>
            <span v-else>tsxm</span>
          </div>
        </div>
        <div class="login-con-body">
          <div v-if="!english">
            <mt-field class="login-field" 
              label="邮箱" 
              placeholder="请输入邮箱" 
              @mouseout.native="sendEmailCode"
             v-model="email">
            </mt-field>
            <mt-field class="login-field" 
              label="邮箱验证码" 
              placeholder="请输入邮箱验证码" 
              v-model="emailCode">
            </mt-field>
            <mt-field class="login-field" 
              label="昵称" 
              placeholder="请输入昵称" 
              v-model="nickName" :attr="{ maxlength: 10 }">
            </mt-field>
            <mt-field class="login-field" 
              label="密码" 
              placeholder="请输入密码" 
              type="password" v-model="password">
            </mt-field>
            <!-- <mt-field class="login-field" 
              label="验证码" 
              v-model="captcha">
              <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
            </mt-field> -->
          </div>
          <div v-else>
            <mt-field class="login-field" 
              label="邮箱" 
              placeholder="Please enter the email" 
              @mouseout.native="sendEmailCode"
             v-model="email">
            </mt-field>
            <mt-field class="login-field" 
              label="eamil code" 
              placeholder="Please enter the email code" 
              v-model="emailCode">
            </mt-field>
            <mt-field class="login-field" 
              label="昵称" 
              placeholder="Please enter the nickname" 
              v-model="username" :attr="{ maxlength: 10 }">
            </mt-field>
            <mt-field class="login-field" 
              label="密码" 
              placeholder="Please enter the password" 
              type="password" v-model="password">
            </mt-field>
            <!-- <mt-field class="login-field" 
              label="验证码" 
              v-model="captcha">
              <img :src="base64Data" height="45px" width="100px" @click="getCodeSrc()">
            </mt-field>        -->
          </div>
          <div class="login-agreement-con">
            <input type="checkbox" v-model="agreeValue">
            <span>同意协议</span>
            <span 
              class="login-field"
              @click="popupVisible = true">
              <span v-if="!english">查看协议内容</span>
              <span v-else>right popup</span>
            </span>
          </div>
          <div class="tac login-btn">
            <mt-button type="default" 
              @click.native="registFn">
              <span v-if="!english">注册</span>
              <span v-else>Regist</span>
            </mt-button>
            <mt-button type="default" 
              @click.native="registBackLogin">
              <span v-if="!english">返回登录</span>
              <span v-else>Return</span>
            </mt-button>
          </div>
        </div>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="right" class="agree-popup" :modal="false">
      <mt-button 
        @click.native="popupVisible = false" 
        size="large" type="default">
        <span v-if="!english">关闭 popup</span>
        <span v-else>close popup</span>
      </mt-button>
      <div>
        <span v-if="!english">协议内容</span>
        <span v-else>Protocol content</span>
      </div>
    </mt-popup>
  </div>
</template>

<style lang="scss" scoped>
.login {
  background: #00182c url(../assets/img/bg2.jpg) no-repeat;
  background-size: contain;
  // background: rgba(#ccc, 0.5);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-bottom: 0;

  .lang {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  .login-con {
    width: 80%;
  }

  .login-con-header-title {
    color: #fff;
    font-size: 28px;
  }

  .login-logo {
    display: block;
    margin: 0 auto;
    width: 100px;
    height: 100px;
    line-height: 100px;
    border-radius: 50px;
    border: 1px solid #ccc;
    background: #fff url(../assets/img/avatar.png) center no-repeat;
    background-size: 100% 100%;
  }
  .agree-popup {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .login-agreement-ck {
    display: inline-block;
    vertical-align: middle;
  }
  .login-agreement-con {
    color: rgba(pink, 0.8);
    margin: 20px auto;
    input {
      vertical-align: middle;
    }
  }
  .login-field {
    margin: 10px 0;
    border-radius: 5px;
  }
  .login-btn {
    margin: 20px auto;
  }
}
</style>


